import { Component } from '@angular/core';
import { NavController, IonicPage } from 'ionic-angular';

@IonicPage()
@Component({
  selector: 'page-profile',
  templateUrl: 'profile.html'
})
export class ProfilePage {

  public section: any;

  constructor(
    public navCtrl: NavController
  ) {
    this.section = 'backed';
  }

}



